.star-rating {
	display: inline-block;
	font-size: 24px;
	line-height: 1;
}

.star-rating .star {
	content: '\2605';
	/* Unicode character for a filled star */
	color: #d61515;
}

.star-rating .star.empty {
	content: '\2606';
	/* Unicode character for an empty star */
	color: #0f0101;
}

/* Ensure all stars are visible */
.star-rating .star:nth-child(n) {
	display: inline-block;
}



